class Emitter {
    constructor() {
        this.events = {};
        this.publish = (name, ...args) => {
            if (!this.events[name]) {
                return;
            }
            this.events[name].forEach((callback) => callback(...args));
        };
        this.on = (name, callback) => {
            if (!this.events[name]) {
                this.events[name] = [];
            }
            this.events[name].push(callback);
        };
        this.off = (name, callback) => {
            if (!this.events[name]) {
                return;
            }
            this.events[name] = this.events[name].filter((callbackInner) => callback !== callbackInner);
        };
    }
}
export default Emitter;
